export default `
<script>
{
    let ages = [32, 33, 16, 40]
    write('元素为数值', ages.includes(18), ages.includes(16))
}
{
    let sites = ['baidu', 'yahu', 'google']
    write('元素为字符串', sites.includes('taobao'))
}
{
    let [a, b, c] = [{a:1}, {b: 2}, {c: 3}]
    let arr = [a, b, c]
    write('元素为对象', arr.includes(a))
}
</script>
<hr>
第二个参数是fromIndex，从该位置开始查找
<br>
<script>
{
    //fromIndex 大于等于数组长度，返回false
    let arr = ['a', 'b', 'c']
    write(arr.includes('c', 3), arr.includes('c', 100))
}
{
    //fromIndex 为负值，则需计算索引：array.length + fromIndex 
    //计算出的索引将作为开始搜索searchElement的位置
    // 如果计算出的索引小于 0，则整个数组都会被搜索。
    // 数组长度是3
    // fromIndex 是 -100
    // computed index 是 3 + (-100) = -97
    let arr = ['a', 'b', 'c']
    write(arr.includes('a', -100), arr.includes('c', -100))
}
</script>
`



